body {
  margin: 0;
}

.main {
  justify-content: center;
  display: flex;
}

.main-container {
  --colums: 1;
  grid-template-columns: repeat(var(--colums), 200px);
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main-container {
    --colums: 2;
  }
}

@media (width >= 768px) {
  .main-container {
    --colums: 3;
  }
}

@media (width >= 1024px) {
  .main-container {
    --colums: 4;
  }
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  grid-column: span 1;
  padding-block: 32px 16px;
  padding-inline: 16px;
}

.card-container {
  box-sizing: border-box;
  width: 200px;
  max-width: 200px;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.card-container:hover {
  transition: all .3s;
  transform: scale(1.2);
}

.card__imac {
  justify-content: center;
  width: 100%;
  display: flex;
}

.card__imac-img {
  background-image: url("imac.202c52ff.jpeg");
  background-size: 100% 100%;
  width: 160px;
  height: 134px;
}

.card__title {
  color: #060b35;
  margin-block: 40px 0;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.card__title .card-container:hover {
  color: #34568b;
}

.card__product-code {
  color: #616070;
  margin-block: 4px 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 8px;
  display: flex;
}

.card__price-text {
  color: #616070;
  align-items: center;
  font-size: 12px;
  line-height: 18px;
}

.card__price-sum {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  background: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-weight: 800;
}

.card__button:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
  transition: all .3s;
}

.stars {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars__reviews {
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.stars--0 .stars__star:nth-child(n+1), .stars--1 .stars__star:nth-child(n+2), .stars--2 .stars__star:nth-child(n+3), .stars--3 .stars__star:nth-child(n+4), .stars--4 .stars__star:nth-child(n+5) {
  background-image: url("star.822d561e.svg");
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  float: left;
  cursor: pointer;
  display: flex;
}

.header__logo-img {
  justify-content: center;
  width: 40px;
  height: 40px;
  display: flex;
}

.header__nav {
  text-transform: uppercase;
  height: 100%;
}

.header__nav-link {
  color: #060b35;
  text-align: center;
  cursor: pointer;
  align-items: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  display: flex;
}

.header__nav-list {
  float: right;
  align-items: center;
  margin: 0;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  list-style: none;
  display: flex;
}

.header__nav-list > li {
  margin-left: 20px;
}

.header__nav-list > li:first-child {
  margin-left: 0;
}

.header__nav-item {
  text-align: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: relative;
}

.header__nav-item:hover > a {
  color: #00acdc;
  transition: all .3s;
}

.is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

